const param = require('@polkadot/params/param');

const createSection = require('@polkadot/params/section');

const blockHashAt = {
  description: 'The blockHash for a specific number',
  key: 'sys:old',
  params: [param('blockNumber', 'BlockNumber')],
  type: 'Hash'
};
const blockNumber = {
  description: 'The current block number being processed',
  key: 'sys:num',
  params: [],
  type: 'BlockNumber'
};
const digest = {
  description: 'The digest for the current block',
  key: 'sys:dig',
  params: [],
  type: 'Digest'
};
const extrinsicData = {
  description: 'The data associated with an extrinsic',
  key: 'sys:xtd',
  params: [param('extrinsic', 'u32')],
  type: 'Bytes'
};
const extrinsicIndex = {
  description: 'The extrinsics index',
  key: 'sys:xti',
  params: [],
  type: 'u32'
};
const extrinsicsRoot = {
  description: 'The extrinsicsRoot for the current block',
  key: 'sys:txr',
  params: [],
  type: 'Hash'
};
const accountIndexOf = {
  description: 'The index of the given account',
  key: 'sys:non',
  params: [param('who', 'AccountId')],
  type: 'Index'
};
const parentHash = {
  description: 'The parentHash for the current block',
  key: 'sys:pha',
  params: [],
  type: 'Hash'
};
const randomSeed = {
  description: 'The random seed',
  key: 'sys:rnd',
  params: [],
  type: 'Hash'
};

module.exports = name => createSection(name)(createMethod => ({
  description: 'System',
  public: {
    blockHashAt: createMethod('blockHashAt')(blockHashAt),
    blockNumber: createMethod('blockNumber')(blockNumber),
    digest: createMethod('digest')(digest),
    extrinsicData: createMethod('extrinsicData')(extrinsicData),
    extrinsicIndex: createMethod('extrinsicIndex')(extrinsicIndex),
    extrinsicsRoot: createMethod('extrinsicsRoot')(extrinsicsRoot),
    accountIndexOf: createMethod('accountIndexOf')(accountIndexOf),
    parentHash: createMethod('parentHash')(parentHash),
    randomSeed: createMethod('randomSeed')(randomSeed)
  }
}));