const param = require('@polkadot/params/param');

const createSection = require('@polkadot/params/section');

const transfer = {
  description: 'Transfer',
  params: [param('recipient', 'AccountId'), param('value', 'Balance')],
  type: []
};
const stake = {
  description: 'Stake',
  params: [],
  type: []
};
const unstake = {
  description: 'Unstake',
  params: [],
  type: []
};
const setSessionsPerEra = {
  description: 'Set sessions per era',
  params: [param('sessions', 'BlockNumber')],
  type: []
};
const setBondingDuration = {
  description: 'Set bonding duration',
  params: [param('duration', 'BlockNumber')],
  type: []
};
const setValidatorCount = {
  description: 'Set validator count',
  params: [param('count', 'u32')],
  type: []
};
const forceNewEra = {
  description: 'Force new era',
  params: [],
  type: []
};

module.exports = (name, index) => createSection(name, index)(createMethod => ({
  description: 'Staking',
  public: {
    transfer: createMethod('transfer', 0)(transfer),
    stake: createMethod('stake', 1)(stake),
    unstake: createMethod('unstake', 2)(unstake)
  },
  private: {
    setSessionsPerEra: createMethod('setSessionsPerEra', 0)(setSessionsPerEra),
    setBondingDuration: createMethod('setBondingDuration', 1)(setBondingDuration),
    setValidatorCount: createMethod('setValidatorCount', 2)(setValidatorCount),
    forceNewEra: createMethod('forceNewEra', 3)(forceNewEra)
  }
}));