const u8aConcat = require('@polkadot/util/u8a/concat');

const encodeLength = require('./length'); // flowlint-next-line unclear-type:off


module.exports = function encodeArray(encoder, input) {
  const encoded = u8aConcat.apply(null, input.map(encoder));
  return u8aConcat(encodeLength(encoded.length, 192), encoded);
};