const isUndefined = require('@polkadot/util/is/undefined');

const onMessageResult = require('./onMessageResult');

const onMessageSubscribe = require('./onMessageSubscribe');

module.exports = function onMessage(self) {
  return message => {
    self.l.debug(() => ['received', message.data]); // flowlint-next-line unclear-type:off

    const response = JSON.parse(message.data);
    return isUndefined(response.method) ? onMessageResult(self, response) : onMessageSubscribe(self, response);
  };
};