const u8aToBn = require('@polkadot/util/u8a/toBn');

module.exports = function bytes(input) {
  const length = u8aToBn(input.subarray(0, 4), true).toNumber();
  return {
    length: length + 4,
    value: input.subarray(4, length + 4)
  };
};