const param = require('@polkadot/params/param');

const createSection = require('@polkadot/params/section');

const reportMisbehavior = {
  description: 'Report misbehavior',
  params: [param('report', 'MisbehaviorReport')],
  type: []
};
const setCode = {
  description: 'Set new code',
  params: [param('code', 'Code')],
  type: []
};
const setStorage = {
  description: 'Set storage entries',
  params: [param('entries', ['KeyValue'])],
  type: []
};

module.exports = (name, index) => createSection(name, index)(createMethod => ({
  description: 'Consensus',
  public: {
    reportMisbehavior: createMethod('reportMisbehavior', 0)(reportMisbehavior)
  },
  private: {
    setCode: createMethod('setCode', 0)(setCode),
    setStorage: createMethod('setStorage', 1)(setStorage)
  }
}));