const bnToU8a = require('@polkadot/util/bn/toU8a');

const u8aConcat = require('@polkadot/util/u8a/concat');

const u8aFromString = require('@polkadot/util/u8a/fromString');

const u8aToU8a = require('@polkadot/util/u8a/toU8a');

const addressDecode = require('@polkadot/util-keyring/address/decode');

const _require = require('./defaults'),
      BALANCE_SIZES = _require.BALANCE_SIZES; // flowlint-next-line unclear-type:off


module.exports = function encodeType(type, value, version) {
  try {
    switch (type) {
      case 'AccountId':
        return addressDecode(value);

      case 'Bytes':
      case 'Code':
        return u8aToU8a(value);
      // TODO Here we should do actul length conversions, i.e. 256/512

      case 'Hash':
      case 'Signature':
        return u8aToU8a(value);
      // FIXME Here we should pass through the actual objects

      case 'Call':
      case 'Digest':
      case 'Header':
      case 'KeyValue':
      case 'KeyValueStorage':
      case 'MisbehaviorReport':
      case 'Proposal':
        return u8aToU8a(value);

      case 'Balance':
        return bnToU8a(value, BALANCE_SIZES[version], true);

      case 'BlockNumber':
      case 'SessionKey':
      case 'Timestamp':
      case 'u64':
        return bnToU8a(value, 64, true);

      case 'bool':
        return bnToU8a(value ? 1 : 0, 8, true);

      case 'Index':
      case 'PropIndex':
      case 'ReferendumIndex':
      case 'VoteIndex':
      case 'u32':
        return bnToU8a(value, 32, true);

      case 'String':
        return (() => {
          const u8a = u8aFromString(value);
          return u8aConcat(bnToU8a(u8a.length, 32, true), u8a);
        })();

      case 'u128':
        return bnToU8a(value, 128, true);
      // TODO enums?

      case 'VoteThreshold':
        return bnToU8a(value || 0, 8, true);

      default:
        // NOTE for , if it fails here, we know what is missing
        type; // eslint-disable-line no-unused-expressions

        throw new Error(`No formatter for ${type}`);
    }
  } catch (error) {
    console.error('Failed encoding with', value, 'encoded with', type, error);
    throw error;
  }
};