const _require = require('rxjs/observable/fromPromise'),
      fromPromise = _require.fromPromise;

const isFunction = require('@polkadot/util/is/function');

const cached = require('./cached'); // flowlint-next-line unclear-type:off


module.exports = function observable(subName, name, section) {
  if (isFunction(section[name].unsubscribe)) {
    return cached(subName, name, section);
  } // flowlint-next-line unclear-type:off


  return (...params) => fromPromise(section[name].apply(null, params));
};