const BN = require('bn.js');

const hexFixLength = require('@polkadot/util/hex/fixLength');

const isHex = require('@polkadot/util/is/hex');

const hexToBn = require('@polkadot/util/hex/toBn');

module.exports = function bnDecode(value, bitLength = -1) {
  if (isHex(value)) {
    return hexToBn(hexFixLength(value, bitLength));
  }

  return new BN(value);
};