const bool = require('./bool');

const bn = require('./bn');

const byte = require('./byte');

const bytes = require('./bytes');

const code = require('./code');

const digest = require('./digest');

const header = require('./header');

const keyValue = require('./keyValue');

const method = require('./method');

const misbehavior = require('./misbehavior');

const string = require('./string');

const time = require('./time');

const u8a = require('./u8a');

module.exports = function decodeValue(decode, type, input) {
  switch (type) {
    // TODO Pass back the actual address, not publicKey?
    case 'AccountId':
      return u8a(input, 256);

    case 'Hash':
      return u8a(input, 256);

    case 'Balance':
    case 'u128':
      return bn(input, 128);

    case 'BlockNumber':
    case 'SessionKey':
    case 'u64':
      return bn(input, 64);

    case 'Index':
    case 'PropIndex':
    case 'ReferendumIndex':
    case 'u32':
    case 'VoteIndex':
      return bn(input, 32);

    case 'bool':
      return bool(input);

    case 'Bytes':
      return bytes(input);

    case 'Call':
    case 'Proposal':
      return method(decode, input, type === 'Call');

    case 'Code':
      return code(input);

    case 'Digest':
      return digest(input);

    case 'Header':
      return header(input);

    case 'KeyValue':
    case 'KeyValueStorage':
      return keyValue(input);

    case 'MisbehaviorReport':
      return misbehavior(input);

    case 'Signature':
      return u8a(input, 512);

    case 'String':
      return string(input);

    case 'Timestamp':
      return time(input);

    case 'VoteThreshold':
      return byte(input);

    default:
      // NOTE for , if it fails here, we know what is missing
      type; // eslint-disable-line no-unused-expressions

      throw new Error(`No value decoder for type='${type}'`);
  }
};