const hashEncode = require('@polkadot/primitives-rlp/hash/encode');

const u8aConcat = require('@polkadot/util/u8a/concat');

const encodeTransaction = require('../transaction/encode');

const encodeU8a = require('../encoder/u8a');

module.exports = function encodeUnchecked({
  tx,
  signature
}) {
  return encodeU8a(u8aConcat(encodeTransaction(tx), hashEncode(signature, 512)));
};