const param = require('@polkadot/params/param');

const createSection = require('@polkadot/params/section');

const authorityAt = {
  description: 'Authority at a specific index',
  isUnhashed: true,
  key: ':auth:',
  params: [param('index', 'u32')],
  type: 'AccountId'
};
const authorityCount = {
  description: 'The number of authorities',
  isUnhashed: true,
  key: ':auth:len',
  params: [],
  type: 'u32'
};
const code = {
  description: 'The current runtime code',
  isUnhashed: true,
  key: ':code',
  params: [],
  type: 'Code'
};

module.exports = name => createSection(name)(createMethod => ({
  description: 'Consensus',
  public: {
    authorityAt: createMethod('authorityAt')(authorityAt),
    authorityCount: createMethod('authorityCount')(authorityCount),
    code: createMethod('code')(code)
  }
}));