const createHeader = require('@polkadot/primitives-builder/header');

const decodeRaw = require('./decodeRaw');

const _require = require('./offsets'),
      OFF_PARENT_HASH = _require.OFF_PARENT_HASH,
      OFF_STATE_ROOT = _require.OFF_STATE_ROOT,
      OFF_TX_ROOT = _require.OFF_TX_ROOT;

module.exports = function decodeHeader(u8a) {
  const _decodeRaw = decodeRaw(u8a),
        number = _decodeRaw.number,
        logs = _decodeRaw.logs;

  return createHeader({
    digest: {
      logs
    },
    extrinsicsRoot: u8a.subarray(OFF_TX_ROOT, OFF_TX_ROOT + 32),
    number,
    parentHash: u8a.subarray(OFF_PARENT_HASH, OFF_PARENT_HASH + 32),
    stateRoot: u8a.subarray(OFF_STATE_ROOT, OFF_STATE_ROOT + 32)
  });
};