const param = require('@polkadot/params/param');

const createSection = require('@polkadot/params/section');

const setKey = {
  description: 'Set session key',
  params: [param('key', 'SessionKey')],
  type: []
};
const setLength = {
  description: 'Set session length',
  params: [param('length', 'u64')],
  type: []
};
const forceNewSession = {
  description: 'Force new session',
  params: [],
  type: []
};

module.exports = (name, index) => createSection(name, index)(createMethod => ({
  description: 'Session',
  public: {
    setKey: createMethod('setKey', 0)(setKey)
  },
  private: {
    setLength: createMethod('setLength', 0)(setLength),
    forceNewSession: createMethod('forceNewSession', 1)(forceNewSession)
  }
}));