const bnToBn = require('@polkadot/util/bn/toBn');

const isUndefined = require('@polkadot/util/is/undefined');

const txRoot = require('./transaction/root');

const timestampSet = require('./unchecked/timestampSet');

module.exports = function header({
  digest: {
    logs
  } = {},
  extrinsicsRoot,
  number,
  parentHash = new Uint8Array([]),
  stateRoot = new Uint8Array([])
}, transactions = [], timestamp) {
  return {
    digest: {
      logs: logs || []
    },
    extrinsicsRoot: extrinsicsRoot || txRoot(isUndefined(timestamp) ? transactions // $FlowFixMe undefined check above
    : [timestampSet(timestamp)].concat(transactions)),
    number: bnToBn(number || 0),
    parentHash,
    stateRoot
  };
};