const u8aConcat = require('@polkadot/util/u8a/concat');

const encodeParams = require('./params');

module.exports = function encodeExtrinsic(extrinsic, values, version) {
  return u8aConcat(extrinsic.index, encodeParams(extrinsic.params, values, version));
};