const hexToU8a = require('@polkadot/util/hex/toU8a');

const transaction = require('./transaction');

const EVERYBODY = hexToU8a('0xffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff');

module.exports = function inherent(type, data) {
  return transaction(type, data, EVERYBODY, 0);
};