require('./polyfill');

const _connect = require('./connect');

const _on = require('./on');

const _send = require('./send');

const state = require('./state');

const _subscribe = require('./subscribe');

const _unsubscribe = require('./unsubscribe');

module.exports = function wsProvider(endpoint, autoConnect = true) {
  const self = state(endpoint, autoConnect);

  if (autoConnect) {
    _connect(self);
  }

  return {
    connect: () => _connect(self),
    isConnected: () => self.isConnected,
    on: (type, sub) => _on(self, type, sub),
    send: (method, params) => _send(self, method, params),
    subscribe: (method, params, cb) => _subscribe(self, method, params, cb),
    unsubscribe: (method, id) => _unsubscribe(self, method, id)
  };
};