const param = require('@polkadot/params/param');

const createSection = require('@polkadot/params/section');

const setApprovals = {
  description: 'Set approvals',
  params: [param('votes', ['bool']), param('index', 'u32')],
  type: []
};
const reapInactiveVoter = {
  description: 'Remove insactive voter',
  params: [param('signedIndex', 'u32'), param('who', 'AccountId'), param('whoIndex', 'u32'), param('assumedVoteIndex', 'u32')],
  type: []
};
const retractVoter = {
  description: 'Retract voter',
  params: [param('index', 'u32')],
  type: []
};
const submitCandidacy = {
  description: 'Submit candidacy',
  params: [param('slot', 'u32')],
  type: []
};
const presentWinner = {
  description: 'Present winner',
  params: [param('candidate', 'AccountId'), param('total', 'Balance'), param('index', 'u32')],
  type: []
};
const setDesiredSeats = {
  description: 'Set desired seats',
  params: [param('count', 'u32')],
  type: []
};
const removeMember = {
  description: 'Remove member',
  params: [param('member', 'AccountId')],
  type: []
};
const setPresentationDuration = {
  description: 'Set presentation duration',
  params: [param('duration', 'u64')],
  type: []
};
const setTermDuration = {
  description: 'Set term duration',
  params: [param('duration', 'u64')],
  type: []
};

module.exports = (name, index) => createSection(name, index)(createMethod => ({
  description: 'Council',
  public: {
    setApprovals: createMethod('setApprovals', 0)(setApprovals),
    reapInactiveVoter: createMethod('reapInactiveVoter', 1)(reapInactiveVoter),
    retractVoter: createMethod('retractVoter', 2)(retractVoter),
    submitCandidacy: createMethod('submitCandidacy', 3)(submitCandidacy),
    presentWinner: createMethod('presentWinner', 4)(presentWinner)
  },
  private: {
    setDesiredSeats: createMethod('setDesiredSeats', 0)(setDesiredSeats),
    removeMember: createMethod('removeMember', 1)(removeMember),
    setPresentationDuration: createMethod('setPresentationDuration', 2)(setPresentationDuration),
    setTermDuration: createMethod('setTermDuration', 3)(setTermDuration)
  }
}));