const u8aToBn = require('@polkadot/util/u8a/toBn');

const decodeArray = require('../decoder/array');

const decodeU8a = require('../decoder/u8a');

const _require = require('./offsets'),
      OFF_DIGEST = _require.OFF_DIGEST,
      OFF_NUMBER = _require.OFF_NUMBER;

module.exports = function decodeHeader(u8a) {
  const logs = [];
  const dd = decodeArray(u8a.subarray(OFF_DIGEST));
  let offset = 0;

  for (let index = 0; index < dd.length; index++) {
    const log = decodeU8a(dd.data.subarray(offset));
    logs.push(log);
    offset += log.length + 4;
  }

  return {
    body: dd.data.subarray(offset),
    header: u8a.subarray(0, OFF_DIGEST + offset + 4),
    logs,
    number: u8aToBn(u8a.subarray(OFF_NUMBER, OFF_NUMBER + 8), true)
  };
};