const bnToBn = require('@polkadot/util/bn/toBn');

const u8aConcat = require('@polkadot/util/u8a/concat');

const ids = require('./ids');

module.exports = function transaction(type, data, signed, nonce) {
  return {
    data,
    function: u8aConcat(ids[type], data),
    nonce: bnToBn(nonce),
    signed,
    type
  };
};