const param = require('@polkadot/params/param');

const createSection = require('@polkadot/params/section');

const getHead = {
  description: 'Retrieves the best headerHash',
  params: [],
  type: 'Hash'
};
const getHeader = {
  description: 'Retrieves the header for a specific block',
  params: [param('hash', 'Hash')],
  type: 'Header'
};
const newHead = {
  description: 'Retrieves the best header via subscription',
  isSubscription: true,
  params: [],
  type: 'Header'
};
/**
  @summary Methods to retrieve chain data.
*/

module.exports = name => createSection(name)(createMethod => ({
  description: 'Retrieval of chain data',
  public: {
    getHead: createMethod('getHead')(getHead),
    getHeader: createMethod('getHeader')(getHeader),
    newHead: createMethod('newHead')(newHead)
  }
}));