const bnToU8a = require('@polkadot/util/bn/toU8a');

const u8aConcat = require('@polkadot/util/u8a/concat');

module.exports = function encodeTransaction(tx) {
  return u8aConcat(tx.signed, bnToU8a(tx.nonce, 64, true), tx.function);
};