const bnToU8a = require('@polkadot/util/bn/toU8a');

const u8aConcat = require('@polkadot/util/u8a/concat');

const _require = require('./defaults'),
      NONCE_SIZES = _require.NONCE_SIZES;

module.exports = function encodeCall(publicKey, nonce, data, version) {
  return u8aConcat(publicKey, bnToU8a(nonce, NONCE_SIZES[version], true), data);
};