const param = require('@polkadot/params/param');

const createSection = require('@polkadot/params/section');

const approvalsOf = {
  description: 'Approvals for a specific account',
  isDeprecated: true,
  key: 'gov:app:',
  params: [param('who', 'AccountId')],
  type: 'Bytes'
};
const approvalsRatio = {
  description: 'The apprivals ratio',
  isDeprecated: true,
  key: 'gov:apr',
  params: [],
  type: 'u32'
};
const currentProposal = {
  description: 'The current proposal',
  isDeprecated: true,
  key: 'gov:pro',
  params: [],
  type: 'Bytes'
};

module.exports = name => createSection(name)(createMethod => ({
  description: 'Governance (deprecated)',
  isDeprecated: true,
  public: {
    approvalsOf: createMethod('approvalsOf')(approvalsOf),
    approvalsRatio: createMethod('approvalsRatio')(approvalsRatio),
    currentProposal: createMethod('currentProposal')(currentProposal)
  }
}));