const bnEncode = require('../bn/encode');

const hashEncode = require('../hash/encode');

module.exports = function headerEncode({
  digest: {
    logs
  },
  extrinsicsRoot,
  number,
  parentHash,
  stateRoot
}) {
  return [hashEncode(parentHash, 256), bnEncode(number, 64), hashEncode(stateRoot, 256), hashEncode(extrinsicsRoot, 256), [logs.map(log => hashEncode(log))]];
};