const _decodeResponse = require('./decodeResponse');

const _encodeJson = require('./encodeJson');

const _encodeObject = require('./encodeObject');

module.exports = function rpcCoder() {
  const self = {
    id: 0
  };
  return {
    decodeResponse: response => _decodeResponse(self, response),
    encodeJson: (method, params) => _encodeJson(self, method, params),
    encodeObject: (method, params) => _encodeObject(self, method, params),
    getId: () => self.id
  };
};