const headerBuilder = require('@polkadot/primitives-builder/header');

const hashDecode = require('../hash/decode');

const bnDecode = require('../bn/decode');

const bytesDecode = require('../bytes/decode');

module.exports = function headerDecode({
  digest,
  extrinsicsRoot,
  number,
  parentHash,
  stateRoot
}) {
  return headerBuilder({
    digest: {
      logs: digest.logs.map(bytesDecode)
    },
    extrinsicsRoot: hashDecode(extrinsicsRoot, 256),
    number: bnDecode(number, 64),
    parentHash: hashDecode(parentHash, 256),
    stateRoot: hashDecode(stateRoot, 256)
  });
};