const param = require('@polkadot/params/param');

const createSection = require('@polkadot/params/section');

const cooloffPeriod = {
  description: 'The coolof period',
  key: 'cov:cooloff',
  params: [],
  type: 'BlockNumber'
};
const votingPeriod = {
  description: 'The voting period',
  key: 'cov:period',
  params: [],
  type: 'BlockNumber'
};
const proposals = {
  description: 'The current proposals',
  key: 'cov:prs',
  params: [],
  type: [['BlockNumber', 'Hash']]
};
const proposalOf = {
  description: 'The proposal by hash',
  key: 'cov:pro',
  params: [param('hash', 'Hash')],
  type: 'Proposal'
};
const proposalVoters = {
  description: 'Voters on a proposal',
  key: 'cov:voters:',
  params: [param('hash', 'Hash')],
  type: ['AccountId']
};
const councilVoteOf = {
  description: 'Council votes on proposal',
  key: 'cov:vote:',
  params: [param('hash', 'Hash'), param('who', 'AccountId')],
  type: 'bool'
};
const vetoedProposal = {
  description: 'Council vetos on a proposal',
  key: 'cov:veto:',
  params: [param('hash', 'Hash')],
  type: ['BlockNumber', 'AccountId']
};

module.exports = name => createSection(name)(createMethod => ({
  description: 'Council voting',
  public: {
    cooloffPeriod: createMethod('cooloffPeriod')(cooloffPeriod),
    votingPeriod: createMethod('votingPeriod')(votingPeriod),
    proposals: createMethod('proposals')(proposals),
    proposalOf: createMethod('proposalOf')(proposalOf),
    proposalVoters: createMethod('proposalVoters')(proposalVoters),
    councilVoteOf: createMethod('councilVoteOf')(councilVoteOf),
    vetoedProposal: createMethod('vetoedProposal')(vetoedProposal)
  }
}));