const createSection = require('@polkadot/params/section');

const current = {
  description: 'The current timestamp',
  key: 'tim:val',
  params: [],
  type: 'Timestamp'
};
const didUpdate = {
  description: 'Did the timestamp update',
  key: 'tim:did',
  params: [],
  type: 'bool'
};

module.exports = name => createSection(name)(createMethod => ({
  description: 'Timestamp related entries',
  public: {
    current: createMethod('current')(current),
    didUpdate: createMethod('didUpdate')(didUpdate)
  }
}));