const _require = require('rxjs/BehaviorSubject'),
      BehaviorSubject = _require.BehaviorSubject;

module.exports = function connected(provider) {
  const subject = new BehaviorSubject(provider.isConnected());
  provider.on('connected', () => subject.next(true));
  provider.on('disconnected', () => subject.next(false));
  return subject;
};