const param = require('@polkadot/params/param');

const createSection = require('@polkadot/params/section');

const propose = {
  description: 'Propose',
  params: [param('proposal', 'Proposal'), param('value', 'Balance')],
  type: []
};
const second = {
  description: 'Second',
  params: [param('proposal', 'u32')],
  type: []
};
const vote = {
  description: 'Vote',
  params: [param('referendumIndex', 'ReferendumIndex'), param('vote', 'bool')],
  type: []
};
const startReferendum = {
  description: 'Start referendum',
  params: [param('proposal', 'Proposal'), param('voteThreshold', 'VoteThreshold')],
  type: []
};
const cancelReferendum = {
  description: 'Cancel referendum',
  params: [param('referendumIndex', 'ReferendumIndex')],
  type: []
};

module.exports = (name, index) => createSection(name, index)(createMethod => ({
  description: 'Democracy',
  public: {
    propose: createMethod('propose', 0)(propose),
    second: createMethod('second', 1)(second),
    vote: createMethod('vote', 2)(vote)
  },
  private: {
    startReferendum: createMethod('startReferendum', 0)(startReferendum),
    cancelReferendum: createMethod('cancelReferendum', 1)(cancelReferendum)
  }
}));