const param = require('@polkadot/params/param');

const createSection = require('@polkadot/params/section');

const currentIndex = {
  description: 'Current index of the session',
  key: 'ses:ind',
  params: [],
  type: 'BlockNumber'
};
const lastSessionChange = {
  description: 'Block at which the session length last changed',
  key: 'ses:llc',
  params: [],
  type: 'BlockNumber'
};
const nextKeyFor = {
  description: 'The next key for a given validator',
  key: 'ses:nxt:',
  params: [param('who', 'AccountId')],
  type: 'SessionKey'
};
const nextLength = {
  description: 'The next session length',
  key: 'ses:nln',
  params: [],
  type: 'BlockNumber'
};
const length = {
  description: 'Current length of the session',
  key: 'ses:len',
  params: [],
  type: 'BlockNumber'
};
const validators = {
  description: 'Current validators',
  key: 'ses:val',
  params: [],
  type: ['AccountId']
};

module.exports = name => createSection(name)(createMethod => ({
  description: 'Session management information',
  public: {
    currentIndex: createMethod('currentIndex')(currentIndex),
    lastSessionChange: createMethod('lastSessionChange')(lastSessionChange),
    nextKeyFor: createMethod('nextKeyFor')(nextKeyFor),
    nextLength: createMethod('nextLength')(nextLength),
    length: createMethod('length')(length),
    validators: createMethod('validators')(validators)
  }
}));