const param = require('@polkadot/params/param');

const createSection = require('@polkadot/params/section');

const propose = {
  description: 'Propose',
  params: [param('proposal', 'Proposal')],
  type: []
};
const vote = {
  description: 'Vote',
  params: [param('proposal', 'Hash'), param('approve', 'bool')],
  type: []
};
const veto = {
  description: 'Veto',
  params: [param('proposal', 'Hash')],
  type: []
};
const setCooloffPeriod = {
  description: 'Set cooloff period',
  params: [param('blocks', 'BlockNumber')],
  type: []
};
const setVotingPeriod = {
  description: 'Set voting period',
  params: [param('blocks', 'BlockNumber')],
  type: []
};

module.exports = (name, index) => createSection(name, index)(createMethod => ({
  description: 'Council Voting',
  public: {
    propose: createMethod('propose', 0)(propose),
    vote: createMethod('vote', 1)(vote),
    veto: createMethod('veto', 2)(veto)
  },
  private: {
    setCooloffPeriod: createMethod('setCooloffPeriod', 0)(setCooloffPeriod),
    setVotingPeriod: createMethod('setVotingPeriod', 1)(setVotingPeriod)
  }
}));